n    margin: 0;\n    padding: 0;\n    background: var(--colour-black);\n}\n\n\n.site-footer-nav ul {\n    display: flex;\n    flex-grow: 1;    \n    align-items: center;    \n    justify-content: center;\n    flex-wrap: wrap;\n    text-align: center;    \n    margin: 0;\n    padding: 0;\n    list-style: none;\n    background: var(--colour-black);\n}\n\n.site-footer-nav ul li {\n    display: inline-flex;\n    align-items: center;\n    padding: 0;\n    margin: 0 5em 0 0;\n    line-height: 2em;\n    background: var(--colour-black);\n    height: 100%;\n}\n\n.site-footer-nav ul li:last-child {\n  margin: 0;\n}\n\n.site-footer-nav a {\n    display: block;\n    position: relative;\n    align-items: center;\n    font-size: 1.1em;\n    color: var(--colour-white);\n    padding: 0 .5em;\n    height: 100%;\n}\n\n.site-footer-nav a:hover {\n    color: var(--colour-red-light);\n}\n\n\n.site-footer-nav li.nav-ijf-home a {\n    background-image: url(\"/assets/gfx/ijf-logo-whitelight.png\");\n    background-repeat: no-repeat;\n    background-position: 50%;    \n    margin-left: 3em;\n    margin-right: 1em;\n}\n\n\n.site-footer .copyright {\n    width: 100%;\n    text-align: center;\n    border-top: 1px solid var(--colour-black);\n    margin-top: 1em;\n    padding-top: 1em;    \n    color: var(--colour-text);\n    /* letter-spacing: -0.015em; */\n    font-size: 1em;\n    font-weight: 500;    \n}\n\n.site-footer .copyright a {\n    color: var(--colour-text);\n    /* letter-spacing: -0.015em; */\n    font-size: 1em;\n    font-weight: 500;\n}\n\n.site-footer .copyright a:hover {\n    color: var(--colour-red-dark);\n}\n\n\n@media (max-width: 800px) {\n    .site-footer .inner {\n        /* max-width: 500px; */\n        grid-template-columns: 1fr;\n        grid-gap: 0;\n        text-align: center;\n    }\n    .site-footer .copyright,\n    .site-footer .copyright a {\n        color: #fff;\n        font-size: 1.3rem;\n        text-align: center;\n    }\n}\n"]}